.CartItemCard{
    display: flex;
    padding: 1vmax;
    height: 8vmax;
    align-items: flex-start;
}
.CartItemCard>img{
    width: 5vmax;

}

.CartItemCard>div{
    display: flex;
    flex-direction: column;
    margin: 0vmax 1vmax;
}
.CartItemCard>div>a{
    font: 300 0.9vmax cursive;
    color: rgba(24, 24, 24, 0.815);
    text-decoration: none;
}

.CartItemCard>div>span{
    font: 300 0.9vmax "Roboto";
    color: rgba(24, 24, 24, 0.815);
}
.CartItemCard>div>p{
    color: tomato;
    font: 100 0.8vmax "Roboto";
    cursor: pointer;
}

@media screen and (max-width:600px) {
    .CartItemCard{
        padding: 3vmax;
        height: 25vmax;
    }
    .CartItemCard>img{
        width: 10vmax;

    }
    .CartItemCard>div{
        margin: 1vmax 2vmax;
    }

    .CartItemCard>div>a{
        font: 300 2vmax cursive;
    }

    .CartItemCard>div>span{
        font: 300 1.9vmax "Roboto";
    }

    .CartItemCard>div>p{
        font: 100 1.8vmax "Roboto";
    }
}