.sidebar{
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
}

.sidebar>a:first-child{
    padding: 0;
}

.sidebar>a>img{
    width: 100%;
    transition: all 0.5s; 
}

.sidebar>a>img:hover{
    filter: drop-shadow(0 0 10px tomato);
}
.sidebar a{
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    transition: all 0.5s;
    padding: 2rem;
    font: 200 1rem "Roboto";
}
.Firsttree{
    color: rgba(0, 0, 0, 0.493);
    transition: all 0.5s;
    padding: 2rem;
    font: 200 1rem "Roboto" !important;

}
.MuiTypography-body1{
    font: 200 1rem "Roboto" !important ;
}

.sidebar a:hover{
    color: tomato;
    transform: scale(1.1);
}
.Firsttree:hover{
    color: tomato;
    transform: scale(1.1);
}

.sidebar a> p{
    display: flex;
    align-items: center;
}
.Firsttree a>p{
    display: flex;
    align-items: center;
}
.sidebar a>p>svg{
    margin-right: 0.5rem;
}
.Firsttree a>p>svg{
    margin-right: 0.5rem;
}

.MuiTypography-root{
    background-color: #fff !important;
}